export default {
	name:'One',
	data() {
		return {
			nav:[{id:1,text:"早餐"},{id:2,text:"午餐"},{id:3,text:"晚餐"}],
			"isSelected":1,
			nav2:[{idx:1,text2:'一楼'},{idx:2,text2:'二楼'}],
			"isSelected2":1,
			"foodlist":[]
		}
	},
	methods: {
		/* 左边 早餐、午餐、晚餐*/
		selected(id){
			/* 高亮 */
			this.isSelected=id
			// console.log(this.isSelected)
			/* 在request里无法使用this,解决办法：在request前将this存起来 */
			let self=this
			if(id==1){
				id=1
			}
			uni.request({
				url: 'http://localhost:10086/list',
				data:{
					id:String(id)
				},
				success(res) {
				console.log('res',res.data.data)
				self.foodlist=res.data.data
				// console.log('this',self)
				}
			})
		},
		/* 右边 一楼、二楼*/
		change(id){
			this.isSelected2=id
		},
		/* 跳转详情页  =>传参 */
		toDetail(id){
			console.log('id',id)
			uni.navigateTo({
				url:`../../component/detail/detail?id=${id}`
			})
		}
	},
	/* 页面首次加载进入请求数据 */
	created() {
		/* 坑 */
		/* 在request里无法使用this,解决办法：在request前将this存起来 */
		let self=this
		uni.request({
			 url:'http://localhost:10086/list',
			 data:{
				 id:'1'
			 },
			 success(res) {
			 	console.log('res-first',res.data.data[0])
				self.foodlist=res.data.data
				// console.log('this.foodlist',self.foodlist)
			 }
		})
	}

}
